<template>
  <v-app id="main-view">
    <!-- <core-filter /> -->
    <core-toolbar />
    <v-content
      :style="'padding-left: 0px;'">
      <v-fade-transition mode="out-in">
        <router-view />
      </v-fade-transition>
    </v-content>
    <!-- <core-footer v-if="$route.meta.name !== 'Maps'" /> -->
    <Chatroom v-show="chatView"/>
    <div
      id="chat-btn"
      @click="doOpenChat()
      ">
      X
    </div>
    <core-footer />
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import Chatroom from '../components/Plugins/Chatroom.vue'
export default {
  name: 'MainView',
  components: {
    Chatroom
  },
  metaInfo () {
    return {
      title: this.appName
    }
  },
  data: () => ({
    chatView: false
  }),
  computed: {
    ...mapState(['appName', 'dologinView', 'doChatView'])
  },
  watch: {
    doChatView (val) {
      this.chatView = val
    },
    chatView (val) {
      this.$store.state.doChatView = val
    }
  },
  methods: {
    doOpenChat () {
      if (localStorage.getItem('token')) {
        this.chatView = !this.chatView
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
